import React, { useState,useEffect } from 'react'
import {
  CCol,
  CNav,
  CNavItem,
  CNavLink,
  CRow,
  CTabContent,
  CTabPane,
  CCard,
  CCardBody,
  CTabs,
  CCardHeader,
  CButton,
  CInput,
  CModal,
  CModalHeader,
  CModalFooter,
  CFormGroup,
  CLabel,
  CForm,
  CModalTitle,
  CModalBody
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import MaterialTable from 'material-table'
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import { useDispatch, useSelector } from "react-redux";
import {
  GetBobotKecukupan,
  GetBobotKetersediaanAction,
  GetBobotKondisiAction,
  setFormKecukupanAction,
  PostBobotKecukupanAction,
  SetBobokKecukupanByIdAction,
  UpdateBobotKecukupanAction,
  setFormKetersediaanAction,
  setFormKondisiAction,
  SetBobotKetersediaanByIdAction,
  PostBobotKetersediaanAction,
  UpdateBobotKetersediaanAction,
  DeleteKecukupanAction,
  DeleteKetersediaanAction,
  PostBobotKondisiAction,
  SetBobotKondisiByIdAction,
  UpdateBobotKondisiAction,
  DeleteKondisiAction,
} from "src/common/redux/action";


const Bobot = () => {

  const {
    GetBobotKecukupans,
    GetBobotKetersediaan,
    SetBobotKondisi,
    modalKecukupan,
    modalKetersediaan,
    modalKondisi,
    setFormKecukupan,
    setFormKetersediaan,
    setFormKondisi,
  } = useSelector((state) => state.masterBobotReducer);

  const[stateUpdateKecukupan, setStateUpdateKecukupan] = useState(false);
  const [stateUpdateKetersediaan, setStateUpdateKetersediaan] = useState(false);
  const[stateUpdateKondisi, setStateUpdateKondisi] = useState(false);

  const setBobotKecukupan = () =>
    dispatch(GetBobotKecukupan("GET_LIST_KECUKUPAN"));

  const setBobotKetersediaan = () =>
    dispatch(GetBobotKetersediaanAction("GET_LIST_KETERSEDIAAN"));

  const setBobotKondisis = () =>
    dispatch(GetBobotKondisiAction("GET_LIST_KONDISI"));

    const {
      Id,
      Nama,
      Bobot
    } = setFormKecukupan;



  const dispatch = useDispatch();

  /***
   * all func show modal
   */
  const showModalKecukupan = () =>
    dispatch({ type: "MODAL_KECUKUPAN", payload: !modalKecukupan });

  const showModalKetersediaan = () =>
    dispatch({ type: "MODAL_KETERSEDIAAN", payload: !modalKetersediaan });

  const showModalKondisi = () =>
    dispatch({ type: "MODAL_KONDISI", payload: !modalKondisi });




  const prosesKecukupanDelete = (id, nama) =>
    dispatch(DeleteKecukupanAction(id, nama));

    const prosesKondisiDelete = (id, nama) => dispatch(DeleteKondisiAction(id, nama))

    const prosesKondisiadd = () => dispatch(PostBobotKondisiAction(setFormKondisi))

    const prosesKondisiEdit = () => dispatch(UpdateBobotKondisiAction(setFormKondisi))

  useEffect(() => {
    setBobotKecukupan();
    setBobotKetersediaan();
    setBobotKondisis();
  }, [dispatch]);
  const columnKecukupan = [
    {
      title: "Aksi",
      field: "internal_action",
      cellStyle: {
        backgroundColor: "#039be5",
        color: "#FFF",
      },
      headerStyle: {
        backgroundColor: "#039be5",
      },
      render: (rowData) => {
        return (
          <div className="row">
            <button
              type="button"
              className="btn btn-sm btn-dark mr-2 ml-4"
              onClick={() => HandleModalKecukupan("edit", rowData.id)}
            >
              <CIcon name="cil-pencil" /> Update
            </button>

            <button
              type="button"
              className="btn btn-sm btn-danger"
              onClick={() => prosesKecukupanDelete(rowData.id, rowData.nama)}
            >
              <CIcon name="cil-drop" /> Hapus
            </button>
          </div>
        );
      },
    },
    { title: "Nama Bobot", field: "nama" },
    { title: "Bobot", field: "bobot" },
  ];

  const handleModalBobotKetersediaan = () => {

  }
  const prosesKetersediaanDelete = (id, nama) =>
    dispatch(DeleteKetersediaanAction(id, nama));
  const columnKetersediaan = [
    {
      title: "Aksi",
      field: "internal_action",
      cellStyle: {
        backgroundColor: "#039be5",
        color: "#FFF",
      },
      headerStyle: {
        backgroundColor: "#039be5",
      },
      render: (rowData) => {
        return (
          <div className="row">
            <button
              type="button"
              className="btn btn-sm btn-dark mr-2 ml-4"
              onClick={() => HandleModalKetersediaan("edit", rowData.id)}
            >
              <CIcon name="cil-pencil" /> Update
            </button>

            <button
              type="button"
              className="btn btn-sm btn-danger"
              onClick={() => prosesKetersediaanDelete(rowData.id, rowData.nama)}
            >
              <CIcon name="cil-drop" /> Hapus
            </button>
          </div>
        );
      },
    },
    { title: "Nama Bobot", field: "nama" },
    { title: "Bobot", field: "bobot" },
  ];

  const columnKondisi = [
    {
      title: "Aksi",
      field: "internal_action",
      cellStyle: {
        backgroundColor: "#039be5",
        color: "#FFF",
      },
      headerStyle: {
        backgroundColor: "#039be5",
      },
      render: (rowData) => {
        return (
          <div className="row">
            <button
              type="button"
              className="btn btn-sm btn-dark mr-2 ml-4"
              onClick={() => HandleModalKondisi("edit", rowData.id)}
            >
              <CIcon name="cil-pencil" /> Update
            </button>

            <button
              type="button"
              className="btn btn-sm btn-danger"
              onClick={() => prosesKondisiDelete(rowData.id, rowData.nama)}
            >
              <CIcon name="cil-drop" /> Hapus
            </button>
          </div>
        );
      },
    },
    { title: "Nama Bobot", field: "nama" },
    { title: "Bobot", field: "bobot" },
  ];

  const prosesKecukupanEdit = () =>
    dispatch(UpdateBobotKecukupanAction(setFormKecukupan));

  const prosesKecukupanAdd = () => dispatch(PostBobotKecukupanAction(setFormKecukupan));

  const prosesKetersediaanadd = () => dispatch(PostBobotKetersediaanAction(setFormKetersediaan))

  const prosesKetersediaanEdit = () =>
    dispatch(UpdateBobotKetersediaanAction(setFormKetersediaan));

  const HandleModalKecukupan = (type, id) =>{
    if(type == "add_kecukupan"){
      showModalKecukupan();
      setStateUpdateKecukupan(false);
      dispatch({type:"RESET_FORM_KECUKUPAN"})
    }else{
      setStateUpdateKecukupan(true);
      dispatch(SetBobokKecukupanByIdAction(id));
      showModalKecukupan()
    }
  }

   const HandleModalKetersediaan = (type, id) => {
     if (type == "add_ketersediaan") {
       showModalKetersediaan();
       setStateUpdateKetersediaan(false);
       dispatch({ type: "RESET_FORM_KETERSEDIAAN" });
     } else {
       setStateUpdateKecukupan(true);
       dispatch(SetBobotKetersediaanByIdAction(id));
       showModalKetersediaan();
     }
   };

    const HandleModalKondisi = (type, id) => {
      console.log(type)
      if (type == "add_Kondisi") {
        showModalKondisi();
        setStateUpdateKondisi(false);
        dispatch({ type: "RESET_FORM_Kondisi" });
      } else {
        setStateUpdateKondisi(true);
        dispatch(SetBobotKondisiByIdAction(id));
        showModalKondisi();
      }
    };


  return (
    <CRow>
      <CCol xs="12" md="12" className="mb-4">
        <CCard>
          <CCardHeader>Master Bobot</CCardHeader>
          <CCardBody>
            <CTabs>
              <CNav variant="tabs">
                <CNavItem>
                  <CNavLink>Bobot Kecukupan</CNavLink>
                </CNavItem>
                <CNavItem>
                  <CNavLink>Ketersediaan</CNavLink>
                </CNavItem>
                <CNavItem>
                  <CNavLink>Kondisi</CNavLink>
                </CNavItem>
              </CNav>
              <CTabContent>
                <CTabPane>
                  <CCard>
                    <CCardHeader>
                      <CCol md="3">
                        <CButton
                          block
                          color="primary"
                          onClick={() => HandleModalKecukupan("add_kecukupan")}
                        >
                          <FontAwesomeIcon icon="plus" />
                          Tambah Kecukupan
                        </CButton>
                      </CCol>
                    </CCardHeader>
                    <CCardBody>
                      <MaterialTable
                        title=""
                        options={{
                          headerStyle: {
                            backgroundColor: "#01579b",
                            color: "#FFF",
                          },
                          filtering: true,
                        }}
                        columns={columnKecukupan}
                        data={GetBobotKecukupans}
                      />
                    </CCardBody>
                  </CCard>
                </CTabPane>
                <CTabPane>
                  <CCard>
                    <CCardHeader>
                      <CCol md="3">
                        <CButton
                          block
                          color="primary"
                          onClick={() =>
                            HandleModalKetersediaan("add_ketersediaan")
                          }
                        >
                          <FontAwesomeIcon icon="plus" />
                          Tambah Ketersediaan
                        </CButton>
                      </CCol>
                    </CCardHeader>
                    <CCardBody>
                      <MaterialTable
                        title=""
                        options={{
                          headerStyle: {
                            backgroundColor: "#01579b",
                            color: "#FFF",
                          },
                          filtering: true,
                        }}
                        columns={columnKetersediaan}
                        data={GetBobotKetersediaan}
                      />
                    </CCardBody>
                  </CCard>
                </CTabPane>
                <CTabPane>
                  <CCard>
                    <CCardHeader>
                      <CCol md="3">
                        <CButton
                          block
                          color="primary"
                          onClick={() => HandleModalKondisi("add_Kondisi")}
                        >
                          <FontAwesomeIcon icon="plus" />
                          Tambah Kondisi
                        </CButton>
                      </CCol>
                    </CCardHeader>
                    <CCardBody>
                      <MaterialTable
                        title=""
                        options={{
                          headerStyle: {
                            backgroundColor: "#01579b",
                            color: "#FFF",
                          },
                          filtering: true,
                        }}
                        columns={columnKondisi}
                        data={SetBobotKondisi}
                      />
                    </CCardBody>
                  </CCard>
                </CTabPane>
              </CTabContent>
            </CTabs>
          </CCardBody>
        </CCard>
      </CCol>
      // initialize modal
      <CModal show={modalKecukupan} onClose={showModalKecukupan} size="lg">
        <CModalHeader closeButton>
          <CModalTitle>Form Bobot Kecukupan</CModalTitle>
        </CModalHeader>
        <CModalBody>
          <CForm>
            <CFormGroup>
              <CLabel htmlFor="nf-namaBobot">Nama Bobot</CLabel>
              <CInput
                type="text"
                id="nf-Nama"
                name="Nama Bobot"
                placeholder="Input Nama Bobot Kecupkupan.."
                value={Nama}
                onChange={(e) =>
                  dispatch(setFormKecukupanAction("Nama", e.target.value))
                }
              />
            </CFormGroup>
            <CFormGroup>
              <CLabel htmlFor="nf-nama"> Bobot</CLabel>
              <CInput
                type="text"
                id="nf-nama"
                name="Nama"
                value={Bobot}
                placeholder="Input Nama Provinsi .."
                value={Bobot}
                onChange={(e) =>
                  dispatch(setFormKecukupanAction("Bobot", e.target.value))
                }
              ></CInput>
            </CFormGroup>
          </CForm>
        </CModalBody>
        <CModalFooter>
          <CButton
            color="primary"
            onClick={
              stateUpdateKecukupan ? prosesKecukupanEdit : prosesKecukupanAdd
            }
          >
            {stateUpdateKecukupan ? "Update" : "Tambah"}
          </CButton>
          <CButton color="secondary" onClick={showModalKecukupan}>
            Cancel
          </CButton>
        </CModalFooter>
      </CModal>
      // ketersediaan
      <CModal
        show={modalKetersediaan}
        onClose={showModalKetersediaan}
        size="lg"
      >
        <CModalHeader closeButton>
          <CModalTitle>Form Bobot Ketersediaan</CModalTitle>
        </CModalHeader>
        <CModalBody>
          <CForm>
            <CFormGroup>
              <CLabel htmlFor="nf-namaBobot">Nama Bobot</CLabel>
              <CInput
                type="text"
                id="nf-Nama"
                name="Nama Bobot"
                placeholder="Input Nama Bobot Kecupkupan.."
                value={setFormKetersediaan.Nama}
                onChange={(e) =>
                  dispatch(setFormKetersediaanAction("Nama", e.target.value))
                }
              />
            </CFormGroup>
            <CFormGroup>
              <CLabel htmlFor="nf-nama"> Bobot</CLabel>
              <CInput
                type="text"
                id="nf-nama"
                name="Nama"
                value={setFormKetersediaan.Bobot}
                placeholder="Input Bobot .."
                onChange={(e) =>
                  dispatch(setFormKetersediaanAction("Bobot", e.target.value))
                }
              ></CInput>
            </CFormGroup>
          </CForm>
        </CModalBody>
        <CModalFooter>
          <CButton
            color="primary"
            onClick={
              stateUpdateKetersediaan
                ? prosesKetersediaanEdit
                : prosesKetersediaanadd
            }
          >
            {stateUpdateKetersediaan ? "Update" : "Tambah"}
          </CButton>
          <CButton color="secondary" onClick={showModalKetersediaan}>
            Cancel
          </CButton>
        </CModalFooter>
      </CModal>
      // Kondisi
      <CModal show={modalKondisi} onClose={showModalKondisi} size="lg">
        <CModalHeader closeButton>
          <CModalTitle>Form Bobot Kondisi</CModalTitle>
        </CModalHeader>
        <CModalBody>
          <CForm>
            <CFormGroup>
              <CLabel htmlFor="nf-namaBobot">Nama Bobot</CLabel>
              <CInput
                type="text"
                id="nf-Nama"
                name="Nama Bobot"
                placeholder="Input Nama Bobot Kecupkupan.."
                value={setFormKondisi.Nama}
                onChange={(e) =>
                  dispatch(setFormKondisiAction("Nama", e.target.value))
                }
              />
            </CFormGroup>
            <CFormGroup>
              <CLabel htmlFor="nf-nama"> Bobot</CLabel>
              <CInput
                type="text"
                id="nf-nama"
                name="Nama"
                value={setFormKondisi.Bobot}
                placeholder="Input Bobot .."
                onChange={(e) =>
                  dispatch(setFormKondisiAction("Bobot", e.target.value))
                }
              ></CInput>
            </CFormGroup>
          </CForm>
        </CModalBody>
        <CModalFooter>
          <CButton
            color="primary"
            onClick={
              stateUpdateKondisi
                ? prosesKondisiEdit
                : prosesKondisiadd
            }
          >
            {stateUpdateKondisi ? "Update" : "Tambah"}
          </CButton>
          <CButton color="secondary" onClick={showModalKondisi}>
            Cancel
          </CButton>
        </CModalFooter>
      </CModal>
    </CRow>
  );
}

export default Bobot
